import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import type { WorkDay, Employer, BusinessDetails } from '../types';
import { formatDate, formatCurrency, formatHours } from './calculations';

interface PDFData {
  workDays: WorkDay[];
  employer?: Employer;
  businessDetails: BusinessDetails;
  totals: {
    hours: number;
    km: number;
    beforeVat: number;
    withVat: number;
  };
  startDate: string;
  endDate: string;
}

export async function generatePDF(data: PDFData): Promise<void> {
  const { workDays, employer, businessDetails, totals, startDate, endDate } = data;

  // ×™×¦×™×¨×ª ××œ×× ×˜ HTML ×–×× ×™ ×¢× ×›×œ ×”×ª×•×›×Ÿ
  const container = document.createElement('div');
  container.style.cssText = `
    position: fixed;
    left: -9999px;
    top: 0;
    width: 794px;
    padding: 40px;
    background: white;
    font-family: 'Heebo', Arial, sans-serif;
    direction: rtl;
    box-sizing: border-box;
  `;

  // ×‘× ×™×™×ª ×ª×§×•×¤×”
  let periodText = '×ª×§×•×¤×”: ';
  if (startDate && endDate) {
    periodText += `${formatDate(startDate)} - ${formatDate(endDate)}`;
  } else if (startDate) {
    periodText += `×-${formatDate(startDate)}`;
  } else if (endDate) {
    periodText += `×¢×“ ${formatDate(endDate)}`;
  } else {
    periodText += '×›×œ ×”×ª×§×•×¤×”';
  }

  // ×‘× ×™×™×ª ×˜×‘×œ×”
  const tableRows = workDays.map(day => `
    <tr>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${formatDate(day.date)}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${day.location || '-'}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${day.startTime} - ${day.endTime}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${formatHours(day.regularHours + day.overtimeHours)}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${day.overtimeHours > 0 ? formatHours(day.overtimeHours) : '-'}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${day.kilometers}</td>
      <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${formatCurrency(day.totalWithVat)}</td>
    </tr>
  `).join('');

  container.innerHTML = `
    <div style="display: flex; justify-content: space-between; align-items: flex-start; margin-bottom: 20px;">
      <div style="flex: 1;">
        <h1 style="font-size: 24px; color: #1f2937; margin: 0 0 20px 0;">×“×•×— ×©×¢×•×ª ×¢×‘×•×“×”</h1>
      </div>
      ${businessDetails.logo ? `
        <div style="margin-right: 20px;">
          <img src="${businessDetails.logo}" style="max-width: 200px; max-height: 120px; object-fit: contain;" />
        </div>
      ` : ''}
    </div>

    <div style="margin-bottom: 20px; font-size: 14px; color: #374151;">
      ${businessDetails.name ? `<div style="font-weight: bold; font-size: 16px; margin-bottom: 5px;">${businessDetails.name}</div>` : ''}
      ${businessDetails.businessNumber ? `<div>×¢.×. / ×—.×¤.: ${businessDetails.businessNumber}</div>` : ''}
      ${businessDetails.address ? `<div>${businessDetails.address}</div>` : ''}
      ${businessDetails.phone ? `<div>×˜×œ×¤×•×Ÿ: ${businessDetails.phone}</div>` : ''}
      ${businessDetails.email ? `<div>${businessDetails.email}</div>` : ''}
    </div>

    <div style="margin-bottom: 20px; padding: 10px; background: #f3f4f6; border-radius: 6px;">
      <div style="font-size: 14px;">${periodText}</div>
      ${employer ? `<div style="font-size: 14px; margin-top: 5px;">××¢×¡×™×§: ${employer.name}</div>` : ''}
    </div>

    <table style="width: 100%; border-collapse: collapse; margin-bottom: 20px; font-size: 12px;">
      <thead>
        <tr style="background: #3b82f6; color: white;">
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">×ª××¨×™×š</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">××™×§×•×</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">××©×¢×”-×¢×“</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">×©×¢×•×ª</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">× ×•×¡×¤×•×ª</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">×§"×</th>
          <th style="border: 1px solid #3b82f6; padding: 8px; text-align: center;">×¡×”"×›</th>
        </tr>
      </thead>
      <tbody>
        ${tableRows}
      </tbody>
      <tfoot>
        <tr style="background: #e5e7eb; font-weight: bold;">
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">×¡×”"×›</td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;"></td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;"></td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${formatHours(totals.hours)}</td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">-</td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${totals.km}</td>
          <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${formatCurrency(totals.withVat)}</td>
        </tr>
      </tfoot>
    </table>

    <div style="margin-bottom: 20px; text-align: left; font-size: 14px;">
      <div style="margin-bottom: 8px;">×¡×”"×› ×œ×¤× ×™ ××¢"×: ${formatCurrency(totals.beforeVat)}</div>
      <div style="font-weight: bold; font-size: 16px; color: #059669;">×¡×”"×› ×œ×ª×©×œ×•× ×›×•×œ×œ ××¢"×: ${formatCurrency(totals.withVat)}</div>
    </div>

    ${employer ? `
      <div style="margin-top: 20px; padding: 15px; background: #f0f9ff; border-radius: 6px; border: 1px solid #bae6fd;">
        <div style="font-weight: bold; margin-bottom: 10px; font-size: 14px;">×ª×¢×¨×™×¤×™×:</div>
        <div style="font-size: 13px; color: #374151; display: flex; gap: 30px; flex-wrap: wrap;">
          <div>×™×•××™×ª (×¢×“ 12 ×©×¢×•×ª): ${formatCurrency(employer.dailyRate)}</div>
          <div>×©×¢×” × ×•×¡×¤×ª: ${formatCurrency(employer.overtimeRate)}</div>
          <div>×§×™×œ×•××˜×¨: ${formatCurrency(employer.kmRate)}</div>
          <div>××¢"×: ${employer.vatPercent}%</div>
        </div>
        ${employer.bonuses && employer.bonuses.length > 0 ? `
          <div style="margin-top: 12px; padding-top: 12px; border-top: 1px solid #bae6fd;">
            <div style="font-weight: bold; margin-bottom: 8px; font-size: 14px;">×ª×•×¡×¤×•×ª ××¤×©×¨×™×•×ª:</div>
            <div style="font-size: 13px; color: #374151; display: flex; gap: 20px; flex-wrap: wrap;">
              ${employer.bonuses.map(bonus => `<div>${bonus.name}: ${formatCurrency(bonus.amount)}</div>`).join('')}
            </div>
          </div>
        ` : ''}
      </div>
    ` : ''}

    ${(businessDetails.bankName || businessDetails.bankBranch || businessDetails.bankAccount) ? `
      <div style="margin-top: 20px; padding: 15px; background: #f9fafb; border-radius: 6px; border: 1px solid #e5e7eb;">
        <div style="font-weight: bold; margin-bottom: 10px; font-size: 14px;">×¤×¨×˜×™ ×”×¢×‘×¨×” ×‘× ×§××™×ª:</div>
        <div style="font-size: 13px; color: #374151;">
          ${businessDetails.bankName ? `<div>×‘× ×§: ${businessDetails.bankName}</div>` : ''}
          ${businessDetails.bankBranch ? `<div>×¡× ×™×£: ${businessDetails.bankBranch}</div>` : ''}
          ${businessDetails.bankAccount ? `<div>×—×©×‘×•×Ÿ: ${businessDetails.bankAccount}</div>` : ''}
        </div>
      </div>
    ` : ''}
  `;

  document.body.appendChild(container);

  try {
    // ×”××ª× ×” ×œ×˜×¢×™× ×ª ×”×ª××•× ×•×ª
    await new Promise(resolve => setTimeout(resolve, 100));

    // ×™×¦×™×¨×ª canvas ××”HTML
    const canvas = await html2canvas(container, {
      scale: 2,
      useCORS: true,
      allowTaint: true,
      backgroundColor: '#ffffff',
    });

    // ×™×¦×™×¨×ª PDF
    const imgData = canvas.toDataURL('image/png');
    const pdf = new jsPDF({
      orientation: 'portrait',
      unit: 'mm',
      format: 'a4',
    });

    const pdfWidth = pdf.internal.pageSize.getWidth();
    const pdfHeight = pdf.internal.pageSize.getHeight();
    const imgWidth = canvas.width;
    const imgHeight = canvas.height;
    const ratio = Math.min(pdfWidth / imgWidth, pdfHeight / imgHeight);
    const imgX = (pdfWidth - imgWidth * ratio) / 2;
    const imgY = 0;

    pdf.addImage(imgData, 'PNG', imgX, imgY, imgWidth * ratio, imgHeight * ratio);

    // ×©××™×¨×”
    const fileName = employer
      ? `×“×•×—_×©×¢×•×ª_${employer.name}_${new Date().toISOString().split('T')[0]}.pdf`
      : `×“×•×—_×©×¢×•×ª_${new Date().toISOString().split('T')[0]}.pdf`;

    pdf.save(fileName);
  } finally {
    document.body.removeChild(container);
  }
}
